import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialogConfig } from '@angular/material/dialog';
import { BatchDetailDialogComponent } from 'src/app/shared/components/dialogs/batch-detail-dialog/batch-detail-dialog.component';
@Component({
  selector: 'batch-report',
  templateUrl: './user-batch-reports.component.html',
  styleUrls: ['./user-batch-reports.component.css']
})
export class UserBatchReportsComponent extends BaseListClass implements OnInit {

  public componentName : string;
  public displayedColumns = ['S No', 'User_id', 'Batch', 'Status' ,'Action'];
  public dataSource = new MatTableDataSource();
  public showTable: boolean;
  public params = {};

  constructor(
    public injector : Injector
  ) {
    super(injector);
  }

  ngOnInit(): void {
    this.componentName = "Batch Report List";
  }

  getBatchData(event){
    this.params = event;
    this._http.get({
      endpoint: URLz.BATCH_REPORTS,
      query: {
        organisation_id:event.organisation_id,
        system_id: event.system_id,
        bg:event.bg,
        le:event.le,
        ou:event.ou,
        su:event.su,
        person_id:event.user_id,
        from_date:event.batch_start,
        to_date:event.batch_to
      }
    }).subscribe((res:any)=>{
    res?.data?.records.length > 0 ? (
      this.dataSource.data = res?.data?.records,
      this.showTable = true
      ):(
        this.showTable = false,
        this._swl.noDataFound()
      );
    })
  }

  openDialog(data) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data, params :this.params}
    }
    const dialogRef = this._dialog.open(
      BatchDetailDialogComponent, config);
      dialogRef.afterClosed().subscribe(() => {return});
  }


}
