import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {UserBatchReportsComponent} from './user-batch-reports/user-batch-reports.component';
import { CostCenterReportComponent } from './cost-center-report/cost-center-report.component';
import { BalanceReportComponent } from './balance-report/balance-report.component';
import { UserJadwalReportComponent } from './user-jadwal-report/user-jadwal-report.component';
import { DonationReconcilReportComponent } from './donation-reconcil-report/donation-reconcil-report.component';
import { DonationReconcilSummaryComponent } from './donation-reconcil-summary/donation-reconcil-summary.component';
import { PersonalBatchReportComponent } from './personal-batch-report/personal-batch-report.component';
import { JvReportComponent } from './jv-report/jv-report.component';
import { RegionBalanceReportComponent } from './region-balance-report/region-balance-report.component';
import { ACTION } from 'src/app/enums/action.enum';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'reports',
        pathMatch: 'full',
      },
      {
        path: "user_batch_reports",
        component: UserBatchReportsComponent,
        data: {
          title: "User Batch Reports",
          urls: [
            { title: "User Batch Reports"},
            { title: "List" },
          ],
        },
      },
      {
        path: "personal_batch_report",
        component: PersonalBatchReportComponent,
        data: {
          title: "Personal Batch Report",
          urls: [
            { title: "Personal Batch Report"},
            { title: "List" },
          ],
        },
      },
      {
        path: "cost_center_report",
        component: CostCenterReportComponent,

        data: {
          title: "Cost Center Wise Reports",
          urls: [
            { title: "Cost Center Wise Reports"},
            { title: "List" },
          ],
        },
      },
      {
        path: "trans_report",
        component: BalanceReportComponent,

        data: {
          title: "Transaction Report (User wise)",
          urls: [
            { title: "Transaction Report (User wise)"},
            { title: "List" },
          ],
        },
      },
      {
        path: "user_jadwal_report",
        component: UserJadwalReportComponent,
        data: {
          title: "User Jadwal Report",
          urls: [
            { title: "User Jadwal Report"},
            { title: "List" },
          ],
        },
      },
      {
        path: "balance_report_su",
        component: BalanceReportComponent,

        data: {
          title: "Balance Report SU",
          urls: [
            { title: "Balance Report SU"},
            { title: "List" },
          ],
        },
      },
      {
        path: "balance_report_ou",
        component: BalanceReportComponent,
        data: {
          title: "Balance Report OU",
          permission: [ACTION.VIEW],
          urls: [
            { title: "Balance Report OU"},
            { title: "List" },
          ],
        },
      },
      {
        path: "balance_report_le",
        component: BalanceReportComponent,
        data: {
          title: "Balance Report LE",
          permission: [ACTION.VIEW],
          urls: [
            { title: "Balance Report LE"},
            { title: "List" },
          ],
        },
      },
      {
        path: "balance_report_region",
        component: RegionBalanceReportComponent,

        data: {
          title: "Balance Report Region",
          urls: [
            { title: "Balance Report Region"},
            { title: "List" },
          ],
        },
      },
      {
        path: "jv_report",
        component: JvReportComponent,

        data: {
          title: "JV Report",
          urls: [
            { title: "JV Report"},
            { title: "List" },
          ],
        },
      },
      {
        path: "donation_reconcil_report",
        component: DonationReconcilReportComponent,
        data: {
          title: "Donation Reconciliation Report",
          urls: [
            { title: "Donation Reconciliation Report"},
            { title: "List" },
          ],
        },
      },
      {
        path: "reconcil_summary_report",
        component: DonationReconcilSummaryComponent,
        data: {
          title: "Donation Reconciliation Summary Report",
          urls: [
            { title: "Donation Reconciliation Summary Report"},
            { title: "List" },
          ],
        },
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DonationReportsRoutingModule {}
