import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import {Constant} from 'src/app/constant/constant';
import { Custom } from 'src/app/static/custom';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './region-balance-report.component.html',
  styleUrls: ['./region-balance-report.component.css']
})
export class RegionBalanceReportComponent extends BaseForm implements OnInit {

  public componentName : string;
  public External_link : string | any;
  public queryquery ?: any;

  constructor(
    injector : Injector,
  ) {
    super(injector);
   }

  ngOnInit(): void {
    this.componentName = "In/Out Balance Report (Region)";
    this.initForm();
  }


  initForm() {
    this._fs._form = this._fb.group({
      organisation_id: ['',this._vs._vals('Donation Organisation')],
      system_id:['',this._vs._vals('System')],
      bg: ['',this._vs._vals('Business Group')],
      le:['',this._vs._vals('Legal Entity')],
      region:['',this._vs._vals('Region')],
      ou:[''],
      start_date:['',[this._vs._val('Start From Date', {isField:0}),this._vs._val_Date({})]],
      end_date:['',[this._vs._val('To Date', {isField:0}),this._vs._val_Date({})]]
    });
  }

  onSubmit() {
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    const data = this._fs._form.value;
    this._vs.logForm()
    if (this._fs._form.valid) {
      this.getOrigionData(data);
      this._vs._submitted = false;
    }else {
      return this._fs._form.valid;
    }
  }

  getOrigionData(event){
    this.queryquery = {
      ...Constant.defaultJasperParams,
      reportUnit : environment.J_DONATION_SERVER+'/'+environment.J_REPORT+'/in_out_balance_report_region',
      organization_id:event.organisation_id,
      system_id: event.system_id,
      bg:event.bg,
      le:event.le,
      region:event.region,
      ou:event.ou,
      from_date:event.start_date,
      to_date:event.end_date,
    }
    this.External_link = environment.JASPER_URL + URLz.REPORT_URL+ '?' + Custom.objToURLQuery(this.queryquery);
  }

}
