import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import {Constant} from 'src/app/constant/constant';
import { Custom } from 'src/app/static/custom';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './balance-report.component.html',
  styleUrls: ['./balance-report.component.css']
})
export class BalanceReportComponent extends BaseForm implements OnInit {

  public componentName : string;
  public External_link : string | any;
  public queryquery ?: any = {};
  public _path ?: string;
  public reportUnit: any;

  constructor(
    injector : Injector,
  ) {
    super(injector);
   }


  ngOnInit(): void {
    this.ComponentName();
    this.initForm();
    this.removeControls();
  }


  ComponentName(){
    if(this._fhs._hasRoute('balance_report_le')){
      this._component = 'In/Out Balance Report (Legal Entity)';
    }
    else if(this._fhs._hasRoute('balance_report_ou')){
      this._component = 'In/Out Balance Report (Operating Unit)';
    }
    else if(this._fhs._hasRoute('balance_report_su')){
      this._component = 'In/Out Balance Report (Sub Unit)';
    }
    else if(this._fhs._hasRoute('trans_report')){
      this._component = 'Transaction Report (User wise)';
    }
  }

  bankCallBack(val){
    if(this._fhs._hasRoute('trans_report'))
      return val?.name;
    else if(this._fhs._hasRoute('balance_report_su'))
      return val?.title;
  }

  initForm() {
    this._fs._form = this._fb.group({
      organisation_id: ['',this._vs._vals('Donation Organisation')],
      system_id:['',this._vs._vals('System')],
      bg: ['',this._vs._vals('Business Group')],
      le:['',this._vs._vals('Legal Entity')],
      ou:['',this._fhs._hasRoute('balance_report_le') ? '' : this._vs._vals('Operating Unit')],
      su:['',this._fhs._hasRoute('balance_report_ou') ? '' : this._vs._vals('Sub Unit')],
      user_id:['',this._fhs._hasRoute('balance_report_su') ? '' : this._vs._vals('User ID')],
      start_date:['',[this._vs._val('From DateDate', {isField:0}),this._vs._val_Date({})]],
      end_date:['',[this._vs._val('To Date', {isField:0}),this._vs._val_Date({})]],
    });

  }

  removeControls(){
    if(this._fhs._hasRoute('balance_report_le')){
      this._fs._form.removeControl('su');
      this._fs._form.removeControl('user_id');
    }
    else if(this._fhs._hasRoute('balance_report_ou')){
      this._fs._form.removeControl('user_id');
    }
    else if(this._fhs._hasRoute('balance_report_su') || this._fhs._hasRoute('trans_report')){
      this._fs._form.addControl('su',new FormControl('',this._vs._vals('Sub Unit')));
      this._fs._form.addControl('user_id',new FormControl('',this._vs._vals('User ID')));
    }
  }

  onSubmit() {
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    const data = this._fs._form.value;
    if (this._fs._form.valid) {
      this.getBatchData(data);
      this._vs._submitted = false;
    } else {
      return this._fs._form.valid;
    }
  }

  getBatchData(event){
    this.queryquery = {
      ...Constant.defaultJasperParams,
      organization_id:event.organisation_id,
      system_id: event.system_id,
      business_group:event.bg,
      legal_entity:event.le,
      operating_unit:event.ou,
      sub_unit:event.su,
      user_id: this._fs._form.get('user_id') != null && undefined && '' ? Custom.arrayToObj(this._fs._form.get('user_id').value) :'',
      from_date:event.start_date,
      to_date:event.end_date
    }
    this.ReportUnit();
    this.External_link = environment.JASPER_URL + URLz.REPORT_URL+ '?' + Custom.objToURLQuery(this.queryquery);
  }

  ReportUnit(){
    if(this._fhs._hasRoute('balance_report_le')){
      return  this.queryquery.reportUnit = environment.J_DONATION_SERVER+'/'+environment.J_REPORT+'/in_out_balance_report_le';
    }
    else if(this._fhs._hasRoute('balance_report_ou')){
      return  this.queryquery.reportUnit = environment.J_DONATION_SERVER+'/'+environment.J_REPORT+'/in_out_balance_report_ou';
    }
    else if(this._fhs._hasRoute('balance_report_su')){
      return this.queryquery.reportUnit = environment.J_DONATION_SERVER+'/'+environment.J_REPORT+'/in_out_balance_report_su';
    }
    else if(this._fhs._hasRoute('trans_report')){
      return this.queryquery.reportUnit = environment.J_DONATION_SERVER+'/'+environment.J_REPORT+'/transection_report_user_wise_';
    }
  }

}
